<template>
  <div>
    <!-- Èù¢ÂåÖÂ±ëÂØºËà™Âå∫Âüü -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">È¶ñÈ°µ</el-breadcrumb-item>
      <el-breadcrumb-item>ËßíËâ≤ÁÆ°ÁêÜ</el-breadcrumb-item>
      <el-breadcrumb-item>ËßíËâ≤ÂàóË°®</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- Âç°ÁâáÂÜÖÂÆπÂå∫Âüü -->
    <el-card>
      <!-- Ê∑ªÂä†ËßíËâ≤ÊåâÈíÆÂå∫Âüü -->
      <el-row>
        <el-col>
          <el-Button type="primary" @click="addDialogVisible=true">Ê∑ªÂä†üé≠ËßíËâ≤</el-Button>
        </el-col>
      </el-row>
      <!-- ÊùÉÈôêÂàóË°® -->
      <el-table :data="rolesList" border stripe>
        <!-- Â±ïÂºÄÂàó -->
        <el-table-column type="expand" border>
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom',i1 === 0 ? 'bdtop' :'' ,'vencenter']"
              :key="item1.id"
              v-for="(item1 ,i1) in scope.row.children"
            >
              <!-- Ê∏≤Êüì‰∏ÄÁ∫ßÊùÉÈôê -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- Ê∏≤Êüì‰∫åÁ∫ßÂíå‰∏âÁ∫ßÊùÉÈôê -->
              <el-col :span="19">
                <!-- ÈÄöËøáforÂæ™ÁéØÔºåÂµåÂ•óÊ∏≤Êüì‰∫åÁ∫ßÊùÉÈôê -->
                <el-row
                  :class="[ i2 === 0 ? '':'bdtop','vencenter']"
                  :key="item2.id"
                  v-for="(item2, i2) in item1.children"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      @close="removeRightById(scope.row,item3.id)"
                      closable
                      type="warning"
                      :key="item3.id"
                      v-for="item3 in item2.children"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <pre></pre>
          </template>
        </el-table-column>
        <!-- Á¥¢ÂºïÂàó -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="üé≠ËßíËâ≤ÂêçÁß∞" prop="roleName"></el-table-column>
        <el-table-column label="üé≠ËßíËâ≤ÊèèËø∞" prop="roleDesc"></el-table-column>
        <el-table-column label="Êìç‰Ωú" width="300">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
            >ÁºñËæë</el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeRoleById(scope.row.id)"
            >Âà†Èô§</el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-magic-stick"
              @click="showSetRightDialog(scope.row)"
            >ÂàÜÈÖçÊùÉÈôê</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- Ê∑ªÂä†ËßíËâ≤ÂØπËØùÊ°Ü -->
    <el-dialog title="Ê∑ªÂä†Áî®Êà∑" :visible.sync="addDialogVisible" width="50%">
      <!-- ÂÜÖÂÆπ‰∏ª‰ΩìÂå∫ -->
      <el-form :model="addForm" ref="addFormRef" label-width="70px">
        <el-form-item label="ËßíËâ≤ÂêçÁß∞" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="ËßíËâ≤ÊèèËø∞" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- Â∫ïÈÉ®Âå∫Âüü -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="addRole">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
    <!-- ÁºñËæëËßíËâ≤ÂØπËØùÊ°Ü -->
    <el-dialog title="ÁºñËæëËßíËâ≤üé≠" :visible.sync="editDialogVisible" width="50%">
      <!-- ÂÜÖÂÆπ‰∏ª‰ΩìÂå∫ -->
      <el-form :model="editForm" ref="editFormRef" label-width="70px">
        <el-form-item label="ËßíËâ≤ID" prop="roleName">
          <el-input v-model="editForm.roleId" disabled></el-input>
        </el-form-item>
        <el-form-item label="ËßíËâ≤ÂêçÁß∞" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="ËßíËâ≤ÊèèËø∞" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- Â∫ïÈÉ®Âå∫Âüü -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="editRoleInfo">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
    <!-- ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°Ü -->
    <el-dialog
      title="ÂàÜÈÖçÊùÉÈôê"
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClose"
    >
      <!-- ÂÜÖÂÆπ‰∏ª‰ΩìÂå∫ -->
      <el-tree
        :data="rightsList"
        :props="TreeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <!-- Â∫ïÈÉ®Âå∫Âüü -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="allotRights">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { async } from 'q'
export default {
  data() {
    return {
      // üé≠ËßíËâ≤ÂàóË°®
      rolesList: [],
      // Ê∑ªÂä†ËßíËâ≤ÁöÑË°®ÂçïÊï∞ÊçÆ
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      // ÂàÜÈÖçÊùÉÈôêÁöÑÊ†ëÂΩ¢Ë°®Âçï
      TreeProps: {
        label: 'authName',
        children: 'children'
      },
      // ÈªòËÆ§ÈÄâ‰∏≠ÁöÑËäÇÁÇπIDÂÄºÊï∞ÁªÑ
      defKeys: [],
      // ÂàÜÈÖçÊùÉÈôêÂàóË°®
      rightsList: {},
      // ÁºñËæëËßíËâ≤ÁöÑË°®ÂçïÊï∞ÊçÆ
      editForm: {},
      // ÂΩìÂâçÂç≥Â∞ÜÂàÜÈÖçÊùÉÈôêÁöÑËßíËâ≤id
      roleId: '',

      // ÊéßÂà∂Ê∑ªÂä†ËßíËâ≤ÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      addDialogVisible: false,
      // ÊéßÂà∂ÁºñËæëËßíËâ≤ÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      editDialogVisible: false,
      // ÊéßÂà∂ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°ÜÁöÑÊòæÁ§∫‰∏éÈöêËóè
      setRightDialogVisible: false
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // Ëé∑ÂèñÊâÄÊúâËßíËâ≤ÂàóË°®
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        this.$message.error('Ëé∑Âèñüé≠ËßíËâ≤ÂàóË°®Â§±Ë¥•')
      }
      this.rolesList = res.data
      // console.log(this.rolesList)
    },
    // Ê∑ªÂä†ËßíËâ≤
    addRole() {
      this.$refs.addFormRef.validate(async valid => {
        // console.log(valid)
        if (!valid) return
        // Â¶ÇÊûúvalid ‰∏∫true Â∞±ÂèØ‰ª•ÂèëËµ∑Ê∑ªÂä†ËßíËâ≤ÁΩëÁªúËØ∑Ê±Ç
        const { data: res } = await this.$http.post('roles', this.addForm)
        // console.log(res);
        if (res.meta.status !== 201) {
          return this.$message.error('üò´Ê∑ªÂä†Áî®Êà∑Â§±Ë¥•ÔºÅ')
        }
        // ÈöêËóèÊ∑ªÂä†ËßíËâ≤ÁöÑÂØπËØùÊ°Ü
        this.$message.success('üéâÊ∑ªÂä†ËßíËâ≤ÊàêÂäüÔºÅ')
        this.addDialogVisible = false
        this.getRolesList()
      })
    },
    // Â±ïÁ§∫ÁºñËæëËßíËâ≤ÂØπËØùÊ°ÜÁöÑ‰∏éËé∑ÂèñÂØπÂ∫îÁöÑ‰ø°ÊÅØ
    async showEditDialog(id) {
      const { data: res } = await this.$http.get(`roles/${id}`)
      if (res.meta.status !== 200) {
        this.$message.error('Ëé∑Âèñüé≠ËßíËâ≤‰ø°ÊÅØÂ§±Ë¥•')
      }
      this.editDialogVisible = true
      this.editForm = res.data
      console.log(this.editForm)
    },
    // ÁÇπÂáªÊèê‰∫§‰øÆÊîπ‰ø°ÊÅØ
    editRoleInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // ÂèØ‰ª•ÂêëÊúçÂä°Âô®ÂèëËµ∑put‰øÆÊîπËØ∑Ê±Ç‰∫Ü
        const { data: res } = await this.$http.put(
          `roles/${this.editForm.roleId}`,
          {
            roleName: this.editForm.roleName,
            roleDesc: this.editForm.roleDesc
          }
        )
        // console.log(res)
        if (res.meta.status !== 200)
          return this.$message.error('üò´ÁºñËæëËßíËâ≤Â§±Ë¥•')
        this.getRolesList()
        this.editDialogVisible = false
        this.$message.success('üéâÁºñËæëÁî®Êà∑ÊàêÂäü')
      })
    },
    // ÁÇπÂáªÂà†Èô§Áî®Êà∑
    async removeRoleById(id) {
      const confirmResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•ËßíËâ≤üé≠, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch(err => err)
      // Â¶ÇÊûúÁî®Êà∑Á°ÆËÆ§Âà†Èô§ÔºåÂàôËøîÂõûÂ≠óÁ¨¶‰∏≤ confirm
      // Â¶ÇÊûúÁî®Êà∑ÂèñÊ∂àÂà†Èô§ÔºåÂàôËøîÂõûÂ≠óÁ¨¶‰∏≤ cancle
      if (confirmResult !== 'confirm') return this.$message.info('Â∑≤ÁªèÂèñÊ∂àÂà†Èô§')
      // Â¶ÇÊûúÊ≤°ÊúâÂèñÊ∂àÔºåÂ∞±ÂêëÊúçÂä°Âô®ÂèëÈÄÅajaxËØ∑Ê±Ç
      const { data: res } = await this.$http.delete(`roles/${id}`)
      // ÈáçÊñ∞Ëé∑ÂèñÂàóË°®
      this.getRolesList()
      // ÂëäÁü•Áî®Êà∑Âà†Èô§Áä∂ÊÄÅ
      this.$message.success('üéâÂà†Èô§ÊàêÂäü‰∫ÜÔºÅ')
    },
    // Âà†Èô§üé≠ËßíËâ≤ÊùÉÈôê
    async removeRightById(role, rightId) {
      const confirmRemoveResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•ÊùÉÈôê, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmRemoveResult !== 'confirm') {
        return this.$message.info('Â∑≤ÁªèÂèñÊ∂àÂà†Èô§ÔºÅ')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('üò´Âà†Èô§ÊùÉÈôêÂ§±Ë¥•ÔºÅ')

      // this.getRolesList()
      role.children = res.data
      this.$message.success('üéâÂà†Èô§ÊùÉÈôêÊàêÂäüÔºÅ')
    },
    // Â±ïÁ§∫ÂàÜÈÖçüé≠ÊùÉÈôêÂØπËØùÊ°Ü
    async showSetRightDialog(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get(`rights/tree`)
      if (res.meta.status !== 200)
        return this.$message.error('üò´Ëé∑ÂèñÊùÉÈôêÂàóË°®Â§±Ë¥•')
      // ÊääËé∑ÂèñÂà∞ÁöÑÊùÉÈôêÊï∞ÊçÆ‰øùÂ≠òÊê≠ÈÖçrightsList‰∏≠
      this.rightsList = res.data
      // ÈÄíÂΩíËé∑Âèñ‰∏âÁ∫ßËäÇÁÇπÁöÑID
      this.getLeafKeys(role, this.defKeys)

      this.setRightDialogVisible = true
    },
    // ÈÄöËøáÈÄíÂΩíÁöÑÂΩ¢ÂºèÔºåËé∑ÂèñËßíËâ≤‰∏ãÊâÄÊúâÁöÑ‰∏âÁ∫ßÊùÉÈôê‰∏ãÁöÑidÔºåÂπ∂‰øùÂ≠òÂà∞defkeysÊï∞ÁªÑ‰∏≠
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // ÁõëÂê¨ÂàÜÈÖçÊùÉÈôêÂØπËØùÊ°ÜÁöÑÂÖ≥Èó≠‰∫ã‰ª∂
    setRightDialogClose() {
      this.defKeys = []
    },
    // ÁÇπÂáªÁ°ÆÂÆö‰∏∫ËßíËâ≤ÂàÜÈÖçÊùÉÈôê
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: idStr
      })
      if(res.meta.status !== 200) return this.$message.error('üò´ÂàÜÈÖçÊùÉÈôêÂ§±Ë¥•')
      this.getRolesList()
      this.$message.success('üéâÂàÜÈÖçÊùÉÈôêÊàêÂäü')
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
