        this.$message.error('ËØ∑ÈÄâÊã©Ë¶Å‰øÆÊîπÁöÑËßíËâ≤‰ø°ÊÅØÔºÅ')
      }
      console.log(this.userInfo)
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
